<style lang="less" scoped>
    @import './action.less';
</style>

<template>
    <div class="app">
        <!-- <Row class="app-des">
            <span></span>
        </Row> -->
        <Row class="app-action">
            <Row class="app-resource-group-title">
                <h3></h3>
            </Row>
            <div class="app-action-source">
                <Row class="app-action-source-list">
                    <Col span="6" class="app-action-source-item" v-for="(list,index) of actionData" :key="index">

                        <Col span="2" class="app-action-source-item-check">
                            <Checkbox :value="true"></Checkbox>
                        </Col>

                        <Col span="21" class="app-action-source-item-content">
                            <h3>{{ list.action }}</h3>
                            <div class="app-action-source-item-desc">{{ list.desc }}</div>
                        </Col>
                    </Col>
                </Row>

                <Row>
                    <Table :columns="columns" :data="userSources"></Table>
                    <a @click="showModal" class="app-action-white-list"></a>
                </Row>
            </div>
        </Row>
        <!-- modal -->
        <action-modal :modalStatis="showActionModal" @emitPermissionModal="emitPermissionModal"></action-modal>
    </div>
</template>

<script>
import {
  getAppResourcesAndAuthoritys
} from "@/services/appService.js";
import ActionModal from './action-modal';

export default {
  name: "permissionSource",
  components: {
    ActionModal
  },
  props: {
    listId: String
  },
  data() {
    return {
      showActionModal: false,
      actionData: [
        {
          action: "",
          status: "",
          desc: ""
        },
        {
          action: "",
          status: "",
          desc: ""
        },
        {
          action: "",
          status: "",
          desc: ""
        },
        {
          action: "",
          status: "",
          desc: ""
        },
        {
          action: "",
          status: "",
          desc: ""
        },
        {
          action: "",
          status: "",
          desc: ""
        }
      ],
      columns: [
        {
          title: "",
          key: "user"
        },
        {
          title: "",
          key: "source"
        },
        {
          title: "",
          key: "list",
          align: "center",
          render: (h,params) => {
              return h('div',[
                  h('a',{},''),
                  h('span',{
                      style: {
                          height: '20px',
                          borderLeft: '1px solid #39f',
                          margin: '0px 5px'
                      }
                  }),
                  h('a',{},'')
              ])
          }
        }
      ],
      userSources: [
        { user: "", source: ""},
        { user: "", source: ""}
      ]
    };
  },
  methods: {
    showModal() {
      this.showActionModal = true;
    },
    emitPermissionModal() {
      this.showActionModal = false;
    },
    getActionData() {
      getAppResourcesAndAuthoritys(this.listId).then(res => {
        console.log(res);
      })
    }
  },
  created() {},
  mounted() {
    this.getActionData();
  }
};
</script>




